}

.Montserrat{
 font-family: 'Montserrat', sans-serif;
}

.App {
  margin: 0;
  padding: 0;
  width: 100vw;
  height: 100vh;
}

.nav-wrapper{
  width: 100vw;
  height: 100vh;

}

.nav-wrapper img{
  width: 2000px ;
  height: 1333px;
  position: absolute;
}

.images{
    width: 100vw;
    height: 100vh;
    position: relative;
    z-index: -1;
    overflow: hidden;
    margin-bottom: 0;
}
.images img{
 width: 2000px ;
  height: 1333px;
  top: 0px;
  position: absolute;
}

.image-wrapper{
    position: absolute;
    width: 100vw;
    height: 100vh;
    bottom: 0;
}

#esp{
  opacity: 0;

}

.box{
    width: 100vw;
  min-height: 100vh;
  margin: 0;
}

#p1{
  z-index: 1;
}

#p2{
  z-index: 3;
}

#p3{
  z-index: 2;
}

.particle-container{
position: absolute;
width: 100vw;
height: 100vh;
margin: 0;
z-index: 10;
}


.title{
      width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
position: absolute;
color: white;
background-color: rgba(32,35,53,0.4);

}
#title{
  font-size: 7em;
  margin-bottom: 0;
}

#subtitle{
  font-size: 2em;
  margin-top: 0;

}

.b1{
   background-image: url('./pexels2.jpg');
}
.overlay{
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  color: black;
  padding-top: 10vh;
  background-color: rgba(75,89,109,0.8) ;
  color: white;
}

.page1{
    width: 100vw;
  height: 100vh;
margin: 0;
padding: 0;
display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
overflow: hidden;
}

.page1 p{
    max-width: 50vw;
}

.resume{
  font-size: 1.3em;
}


.page2{
    width: 100vw;
  height: 180vh;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  color: black;

background-image: url('./motif2.jpg');
background-repeat: no-repeat;
background-size: cover;
}

.page2 li{
  padding-bottom: 2vh;
}

.page2a{
   min-width: 40vw;
  height: 180vh;

}

#page2b{
  margin-top: 0;
  margin-bottom: 0;
  width: 60vw;
  height: 180vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: white;
  padding-right: 10vw;
  padding-left: 2vw;
  overflow: hidden;
  background-color: rgba(24,25,25,0.9);
}

